<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/productType/standard/list" @click="rest" type='list' :params="{
        id:$route.query.typeId,
         name:$route.query.typeName
    }">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px" @submit.native.prevent>
        <el-form-item label="商品类型:" prop="typeId" :error='errorMessage.typeId'>
          {{$route.query.typeName}}
        </el-form-item>
        <el-form-item label="属性名称:" prop="name" :error='errorMessage.name'>
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label="属性分组:" prop="attrGroupName" :error='errorMessage.attrGroupName'>
          <el-select v-model="formData.attrGroupName">
            <el-option :label="item" :value='item' v-for="(item,index) in groupNames" :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否为销售属性:" prop="isSaleAttr" :error="errorMessage.isSaleAttr">
          <cms-switch v-model="formData.isSaleAttr" @change="tabSale"></cms-switch>
          <div class="gray">选择作为销售属性，将成为构成商品SKU信息元素，不作为商品规格参数展示在商品详情内</div>
        </el-form-item>
        <el-form-item label="是否必填:" prop="required" :error="errorMessage.required">
          <cms-switch v-model="formData.required"></cms-switch>
          <div class="gray">选择是，商品保存时必须要填写相应的属性值，非销售属性推荐非必填</div>
        </el-form-item>
        <el-form-item label="属性值是否开启自定义:" prop="custom" :error="errorMessage.custom">
          <cms-switch v-model="formData.custom"></cms-switch>
          <div class="gray">选择是，允许商家在不满足个性化需求时可自定义添加选项</div>
        </el-form-item>
        <el-form-item label="属性值类型:" prop="valueType" :error="errorMessage.valueType">
          <el-radio-group v-model="formData.valueType" >
            <el-radio :label="2">单选</el-radio>
            <el-radio :label="1" v-if="!formData.isSaleAttr">文本 </el-radio>
            <el-radio :label="3" v-if="!formData.isSaleAttr">多选</el-radio>
            <el-radio :label="4" v-if="formData.isSaleAttr">图片(单选)</el-radio>
          </el-radio-group>
          <div class="gray">选择“文本”，属性值为自定义填写；选择“单选/多选”,属性值只能从属性值选项中选择； 选择“图片”，属性值只能上传图片
          </div>
        </el-form-item>
        <el-form-item label="属性值选项:" prop="attrValues" :error="errorMessage.attrValues"
        :rules="formData.valueType===2|| formData.valueType===3
                 ? [{ required: true, message: '请输入待选项', trigger: 'blur' }]
                 :[]">
          <el-input  maxlength="150" v-model="formData.attrValues" type="textarea" :rows="6" :disabled="formData.valueType===1"></el-input>
          <div class="gray">多个请换行填写，显示先后顺序即填写先后顺序</div>
        </el-form-item>
        <el-form-item label="排序:" prop="sortNum" :error='errorMessage.sortNum'>
          <el-input maxlength="2" v-model.number="formData.sortNum"></el-input>
        </el-form-item>
        <el-form-item label="备注:" prop="remark" :error='errorMessage.namremarke'>
          <el-input  maxlength="255" v-model="formData.remark" type="textarea" :rows="6"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button v-prem="'/productType/standard/save'" @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button v-prem="'/productType/standard/save'" @click="save(true)" v-color>保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import {
  productStandardSave,
  productTypeList
} from '@/api/product/type.js'

var defaultData = {
  name: '',
  isSaleAttr: false,
  required: false,
  custom: false,
  valueType: 2,
  sortNum: 10,
  typeId: 0,
  attrType: 1,
  attrGroupName: '',
  remark: '',
  attrValues: ''
}
export default {
  mixins: [form],
  data() {
    return {
      status: true,
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 新增规则
        name: [rules.required('商品类型名称'), rules.len(1, 150)],
        typeId: [rules.required('必须选择商品类型')],
        isSaleAttr: [rules.required('此项必填')],
        custom: [rules.required('此项必填')],
        valueType: [rules.required('此项必填')],
        sortNum: [rules.required('此项必填'), rules.number()],
        required: [rules.required('此项必填')]
      },
      productTypes: [], // 商品类型列表
      groupNames: []
    }
  },
  methods: {
    tabSale(val) {
      if (val && this.formData.valueType === 3 || this.formData.valueType === 1) {
        this.formData.valueType = 2
      }
    },
    getProductTypes() {
      productTypeList().then(res => {
        this.productTypes = res.data
        this.setGroupNames()
      })
    },
    setGroupNames(value) {
      const arr = this.productTypes.filter(v => v.id === this.formData.typeId)
      this.groupNames = arr[0].groupName.split(',')
    },
    save(state) {
      // 添加
      const params = {
        typeAttr: this.$cms.deepCopy(this.formData),
        attrValues: this.formData.attrValues
      }
      this.saveForm(
        productStandardSave,
        params,
        state,
        '/productType/standard/list',
        'list',
        {
          id: this.$route.query.typeId,
          name: this.$route.query.typeName
        }
      ).then(res => {
        this.formData.typeId = parseInt(this.$route.query.typeId)
      })
    }
  },
  created() {
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
      this.formData.typeId = parseInt(this.$route.query.typeId)
      this.getProductTypes()
      this.destroyed()
    }
  }
}
</script>



